.container {
  background-color: #212529;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  flex-direction: column;
}

.task {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start; 
  width: 100%; 
  min-height: 400px;
}

.task:nth-child(even) {
  background-color: #212529; 
}

.task:nth-child(odd) {
  background-color: #e9ecef;
}

h2 {
  font-size: 24px;
  text-align: center;
  margin: 0; 
}

.task:nth-child(even) h2 {
  color: #e9ecef; 
}

.task:nth-child(odd) h2 {
  color: #212529;
}

